
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
body {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
}
a {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
}
.dropzone-area {
  margin: 50px auto;
  width: 80%;
  height: 200px;
  position: relative;
  border: 2px dashed #CBCBCB;
&.hovered {
    border: 2px dashed #2E94C4;
.dropzone-title {
      color: #1975A0;
}
}
}
.dropzone-area input {
  position: absolute;
  cursor: pointer;
  top: 0px;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
}
.dropzone-text {
  position: absolute;
  top: 50%;
  text-align: center;
  -webkit-transform: translate(0, -50%);
          transform: translate(0, -50%);
  width: 100%;
span {
    display: block;
    font-family: Arial, Helvetica;
    line-height: 1.9;
}
}
.dropzone-title {
  font-size: 13px;
  color: #787878;
  letter-spacing: 0.4px;
}
.dropzone-info {
  font-size: 13px;
  font-size: 0.8125rem;
  color: #A8A8A8;
  letter-spacing: 0.4px;
}
.dropzone-button {
  position: absolute;
  top: 10px;
  right: 10px;
  display: none;
}
.dropzone-preview {
  width: 80%;
  position: relative;
&:hover .dropzone-button {
    display: block;
}
img {
    display: block;
    height: auto;
    max-width: 100%;
}
}



